.inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile + label {
    font-weight: 600;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    display: inline;
    overflow: hidden;
}

.no-js .inputfile + label {
    display: none;
}

.inputfile + label:hover {
    color: #3ba8c5;
}

.box.has-advanced-upload {
    background-color: #e9ecef;
    border-radius: .3rem;
}
.box.has-advanced-upload .box__dragndrop {
    display: inline;
}

.film-add-item-opacity {
    background-color: rgba(0, 0, 0, 0.4);
}

.film-add-item-middle {
    cursor: pointer;
    opacity: 0.8;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}
